header {
  border-right: 1px rgba(255, 255, 255, 0.25) solid;
  width: 160px;
  position: absolute;
  top: 0;
  left: 0;
  right: 160px;
  z-index: 3;
  height: 130vh;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
header div.blue {
  background: #4AF6CD;
  height: 190px;
}
header h1 {
  position: relative;
  top: -60px;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 60px;
  font-style: normal;
  font-weight: 900;
  line-height: 86px;
  /* 143.333% */
  text-transform: uppercase;
  transform: rotate(-90deg);
  text-wrap: nowrap;
}
header nav {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
header nav .nav-el {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}
header nav .nav-el p {
  color: #FFF;
  text-decoration: none;
  text-align: center;
  font-family: Fira Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 3px;
}
header .date div.day {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 72px;
  font-style: normal;
  font-weight: 800;
  line-height: 64px;
  /* 88.889% */
  text-transform: uppercase;
  text-align: center;
}
header .date div.mouth-year {
  display: flex;
  justify-content: center;
  gap: 5px;
  opacity: 0.5;
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 19px;
  /* 118.75% */
  letter-spacing: 1.4px;
}
@media screen and (max-width: 730px) {
  header {
    display: none;
  }
}
