.title {
  margin-top: 160px;
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: end;
}
.title .line {
  width: 80px;
  height: 1px;
  flex-shrink: 0;
  background: #4AF6CD;
}
.title p {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 80px;
  font-style: normal;
  font-weight: 800;
  line-height: 85px;
  text-transform: uppercase;
}
.title h2 {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 200px;
  font-style: normal;
  font-weight: 800;
  line-height: 50px;
  text-transform: uppercase;
  opacity: 0.02;
}
div.all-content {
  width: 100vw;
  height: 100%;
  position: relative;
}
div.all-content .line-back:first-child {
  position: absolute;
  top: -160px;
  left: 160px;
  height: calc(100% + 230px);
  width: 1px;
  background-color: #FFF;
  opacity: 0.067;
}
div.all-content .line-back:nth-child(2) {
  position: absolute;
  top: -160px;
  left: 746px;
  height: calc(100% + 230px);
  width: 1px;
  background-color: #FFF;
  opacity: 0.067;
}
div.all-content .line-back:nth-child(3) {
  position: absolute;
  top: -160px;
  left: 1332px;
  height: calc(100% + 230px);
  width: 1px;
  background-color: #FFF;
  opacity: 0.067;
}
div.all-content .title-min {
  position: relative;
  flex-direction: column;
  margin-right: 5vw;
  display: flex;
  align-items: end;
}
div.all-content .title-min div.over {
  display: flex;
  justify-content: end;
  opacity: 0.5;
  gap: 10px;
  align-items: center;
}
div.all-content .title-min div.over .line-min {
  width: 80px;
  height: 1px;
  opacity: 0.5;
  background: #FFF;
}
div.all-content .title-min div.over p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  /* 145.455% */
  letter-spacing: 7.333px;
  text-transform: uppercase;
}
div.all-content .title-min p.location {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 86px;
  text-transform: uppercase;
}
div.all-content .title-min p.location span {
  font-weight: 100;
  opacity: 0.3;
}
@media screen and (max-width: 406px) {
  .title {
    align-items: center;
  }
}
