main {
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 100%;
  left: 0;
  right: 0;
  height: 130vh;
  margin-left: 0;
  margin-right: 0;
}
main button {
  position: relative;
  z-index: 200;
}
main .btns {
  position: absolute;
  top: 50px;
  right: 100px;
  display: flex;
  z-index: 1010;
}
main .btns button {
  background-color: transparent;
  border: none;
  padding: 20px;
  cursor: pointer;
}
main .background-swiper {
  height: 100vh;
}
main .background-swiper .swiper-slide h2 {
  position: absolute;
  top: 620px;
  right: 5vw;
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 80px;
  font-style: normal;
  font-weight: 800;
  line-height: 86px;
  /* 107.5% */
  text-transform: uppercase;
}
main .background-swiper .swiper-slide img {
  width: 100vw;
  height: 130vh;
}
main .background-swiper .swiper-pagination {
  display: flex;
  justify-content: end;
  position: absolute;
  gap: 50px;
  right: 50px;
  bottom: 50px;
  text-align: end;
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:nth-child(4) {
  margin-right: 5vw;
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet {
  height: 3px;
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 19px;
  /* 118.75% */
  width: 200px;
  border-radius: 8px;
  background-color: white;
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:before {
  padding-right: 80px;
  margin-right: 0;
  flex-direction: column;
  content: '01';
  color: #FFF;
  margin-top: 3px;
  text-align: start;
  font-family: Fira Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 800;
  line-height: 40px;
  /* 141.667% */
  text-transform: uppercase;
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:nth-child(1):before {
  padding-right: 90px;
  content: '01';
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:nth-child(2):before {
  padding-right: 85px;
  content: '02';
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:nth-child(3):before {
  content: '03';
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet:nth-child(4):before {
  padding-right: 90px;
  content: '04';
}
main .background-swiper .swiper-pagination .swiper-pagination-bullet-active {
  background: #4AF6CD;
}
main .content {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 100vw;
  display: flex;
  flex-direction: column;
  z-index: 100;
  align-items: end;
}
main .content .big-text {
  width: 300px;
  position: absolute;
  left: 0;
  top: 0;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 200px;
  font-style: normal;
  font-weight: 900;
  line-height: 156px;
  /* 78% */
  text-transform: uppercase;
  opacity: 0.0654;
  z-index: 1000;
}
main .content .text {
  margin-right: 5vw;
  position: relative;
  top: 550px;
}
main .content .text .surf:first-child {
  margin-bottom: 150px;
}
main .content .text div.surf {
  justify-content: end;
  opacity: 0.5;
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
}
main .content .text div.surf p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  /* 145.455% */
  letter-spacing: 7.333px;
  text-transform: uppercase;
}
main .content .text div.surf .line {
  width: 80px;
  background-color: #FFF;
  height: 1px;
  opacity: 0.5;
}
main .content .text h5 {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 86px;
  /* 215% */
  text-transform: uppercase;
}
main .content svg {
  z-index: 100;
  position: absolute;
  right: 0;
  top: 0;
}
@media screen and (max-width: 1050px) {
  main .map svg {
    display: none;
  }
  main .text h5 {
    display: none;
  }
  main .background-swiper .swiper .swiper-pagination {
    display: none;
  }
  main .background-swiper .swiper .swiper-slide img {
    width: 1920px;
  }
  main .background-swiper .swiper .swiper-slide h2 {
    font-size: 60px;
  }
}
@media screen and (max-width: 410px) {
  main .content .big-text {
    font-size: 150px;
    line-height: 150px;
  }
  .background-swiper .swiper .swiper-slide h2.h2 {
    font-size: 50px;
  }
}
