article.shop#shop {
  position: relative;
}
article.shop#shop .btns {
  display: flex;
  justify-content: flex-end;
  margin-right: 5vw;
  gap: 20px;
}
article.shop#shop .btns button {
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: transparent;
  border: none;
}
article.shop#shop .btns .prev {
  transform: rotate(-180deg);
}
article.shop#shop .bg {
  position: absolute;
  right: 0;
  top: 25%;
}
article.shop#shop .swiper {
  position: relative;
  top: -200px;
  width: 100vw;
}
article.shop#shop .left {
  margin-left: 5vw;
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
article.shop#shop .left .title-min {
  align-items: start;
}
article.shop#shop .left .title-min .over {
  margin-bottom: 120px;
}
article.shop#shop .left .title-min .rating {
  margin-bottom: 200px;
}
article.shop#shop .left a.go {
  top: 0;
}
article.shop#shop .swiper {
  position: relative;
  top: -250px;
}
article.shop#shop .swiper .swiper-slide {
  display: flex;
  justify-content: center;
}
article.shop#shop .swiper .swiper-slide h2 {
  top: -200px;
  text-align: left;
  line-height: 110%;
  width: 350px;
  text-wrap: wrap;
  position: absolute;
  left: 5vw;
  right: auto;
}
article.shop#shop .swiper .swiper-slide h3 {
  display: flex;
  align-items: flex-start;
  gap: 5px;
  left: 5vw;
  top: 0;
  text-align: left;
  line-height: 10%;
  width: 350px;
  position: absolute;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 800;
  line-height: 40px;
  /* 125% */
  text-transform: uppercase;
}
article.shop#shop .swiper .swiper-slide h3 span {
  font-size: 20px;
  line-height: 0;
}
article.shop#shop .swiper .swiper-slide img {
  position: relative;
  top: -350px;
}
article.shop#shop .swiper .info-surf {
  position: absolute;
  top: 20%;
  left: 50%;
  z-index: 5000;
}
article.shop#shop .swiper .info-surf .info {
  position: absolute;
  display: block;
}
article.shop#shop .swiper .info-surf .info .point {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  width: 18px;
  height: 18px;
  background-color: #FFF;
  border-radius: 50%;
  color: #4AF6CD;
}
article.shop#shop .swiper .info-surf .info .point:hover + .hidden {
  opacity: 1;
}
article.shop#shop .swiper .info-surf .info div.hidden {
  opacity: 0;
  transition: opacity 0.5s 0.1s ease;
}
article.shop#shop .swiper .info-surf .info div.hidden .line {
  margin: 10px 0;
  background-color: #fff;
  opacity: 0.5;
  width: 80px;
  height: 1px;
}
article.shop#shop .swiper .info-surf .info div.hidden p {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 22px;
  width: 250px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  /* 100% */
  text-transform: uppercase;
}
article.shop#shop .swiper .info-surf .info:nth-child(1) {
  left: 2vw;
}
article.shop#shop .swiper .info-surf .info:nth-child(2) {
  top: -100px;
  left: -7.5vw;
}
article.shop#shop .swiper .info-surf .info:nth-child(3) {
  top: 100px;
  left: 2.5vw;
}
article.shop#shop .extras {
  position: absolute;
  right: 5vw;
  bottom: 400px;
}
article.shop#shop .extras h2 {
  margin-bottom: 20px;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 28px;
  font-style: normal;
  font-weight: 800;
  line-height: 40px;
  /* 142.857% */
  text-transform: uppercase;
}
article.shop#shop .extras .cont {
  display: flex;
  gap: 80px;
}
article.shop#shop .extras .cont .extrases {
  display: flex;
  flex-direction: column;
  align-items: center;
}
article.shop#shop .extras .cont .extrases div {
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}
article.shop#shop .extras .cont .extrases div img {
  width: 100px;
}
article.shop#shop .extras .cont .extrases p.name {
  opacity: 0.5;
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  /* 122.222% */
  letter-spacing: 8px;
  text-transform: uppercase;
}
article.shop#shop .extras .cont .extrases p.sale {
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 40px;
  /* 222.222% */
  text-transform: uppercase;
}
@media screen and (max-width: 1058px) {
  article.shop#shop .swiper {
    margin-bottom: 500px;
  }
  article.shop#shop .swiper img.slide {
    position: relative;
    top: 300px;
  }
  article.shop#shop .swiper .info-surf {
    top: 800px;
  }
  article.shop#shop .extras#extras {
    bottom: -600px;
  }
}
