article.travel,
article.sleep,
article.shop {
  position: relative;
}
article.travel .title-min#title-min .over,
article.sleep .title-min#title-min .over,
article.shop .title-min#title-min .over {
  margin-bottom: 80px;
}
article.travel .title-min#title-min .btns,
article.sleep .title-min#title-min .btns,
article.shop .title-min#title-min .btns {
  position: relative;
  z-index: 500000;
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: end;
}
article.travel .title-min#title-min .btns button,
article.sleep .title-min#title-min .btns button,
article.shop .title-min#title-min .btns button {
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
article.travel .title-min#title-min .btns .prev,
article.sleep .title-min#title-min .btns .prev,
article.shop .title-min#title-min .btns .prev {
  transform: rotate(180deg);
}
article.travel .swiper,
article.sleep .swiper,
article.shop .swiper {
  position: relative;
  top: -100px;
  width: 100vw;
  overflow-y: visible;
}
article.travel .swiper .swiper-slide,
article.sleep .swiper .swiper-slide,
article.shop .swiper .swiper-slide {
  position: relative;
}
article.travel .swiper .swiper-slide h2,
article.sleep .swiper .swiper-slide h2,
article.shop .swiper .swiper-slide h2 {
  text-wrap: nowrap;
  color: #FFF;
  position: absolute;
  top: -10px;
  right: 5vw;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 86px;
  /* 215% */
  text-transform: uppercase;
}
article.travel .swiper .swiper-slide h2 span,
article.sleep .swiper .swiper-slide h2 span,
article.shop .swiper .swiper-slide h2 span {
  opacity: 0.5;
  font-weight: 100;
}
article.travel .bottom-slide,
article.sleep .bottom-slide,
article.shop .bottom-slide {
  z-index: 50000;
  position: relative;
  top: -400px;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
article.travel .bottom-slide .left,
article.sleep .bottom-slide .left,
article.shop .bottom-slide .left {
  margin-left: 6vw;
}
article.travel .bottom-slide .left .heading,
article.sleep .bottom-slide .left .heading,
article.shop .bottom-slide .left .heading {
  opacity: 0.5;
  display: flex;
  gap: 10px;
  align-items: center;
}
article.travel .bottom-slide .left .heading p,
article.sleep .bottom-slide .left .heading p,
article.shop .bottom-slide .left .heading p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  /* 145.455% */
  letter-spacing: 7.333px;
  text-transform: uppercase;
}
article.travel .bottom-slide .left .heading .line,
article.sleep .bottom-slide .left .heading .line,
article.shop .bottom-slide .left .heading .line {
  width: 80px;
  height: 1px;
  background-color: #FFF;
}
article.travel .bottom-slide .left h5,
article.sleep .bottom-slide .left h5,
article.shop .bottom-slide .left h5 {
  display: flex;
  align-items: center;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 60px;
  /* 215% */
  text-transform: uppercase;
}
article.travel .info,
article.sleep .info,
article.shop .info {
  flex-wrap: wrap;
  position: relative;
  top: -300px;
  display: flex;
  gap: 50px;
  justify-content: center;
}
article.travel .info .block,
article.sleep .info .block,
article.shop .info .block {
  display: flex;
  gap: 10px;
  position: relative;
}
article.travel .info .block h6,
article.sleep .info .block h6,
article.shop .info .block h6 {
  display: inline;
  text-align: right;
  margin: 0;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 8px;
  text-transform: uppercase;
  color: white;
  opacity: 0.5;
}
article.travel .info .block p,
article.sleep .info .block p,
article.shop .info .block p {
  width: 242px;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 36px;
  /* 90% */
  text-transform: uppercase;
}
article.travel a.go,
article.sleep a.go,
article.shop a.go {
  cursor: pointer;
  position: relative;
  top: -200px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
article.travel a.go img.cocktail,
article.sleep a.go img.cocktail,
article.shop a.go img.cocktail {
  position: absolute;
  right: 0;
  top: -300px;
}
article.travel a.go p,
article.sleep a.go p,
article.shop a.go p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 36px;
  /* 90% */
  text-transform: uppercase;
}
article.travel a.go .blue,
article.sleep a.go .blue,
article.shop a.go .blue {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: #4AF6CD;
  width: 168px;
  height: 80px;
}
article.travel a.go .blue .mini-line,
article.sleep a.go .blue .mini-line,
article.shop a.go .blue .mini-line {
  width: 20px;
  height: 1px;
  flex-shrink: 0;
  left: -5%;
  background: #FFF;
  position: absolute;
}
@media screen and (max-width: 533px) {
  article.travel .swiper .swiper-slide,
  article.sleep .swiper .swiper-slide,
  article.shop .swiper .swiper-slide {
    position: relative;
  }
  article.travel .swiper .swiper-slide h2,
  article.sleep .swiper .swiper-slide h2,
  article.shop .swiper .swiper-slide h2 {
    font-size: 30px;
  }
}
