footer a.go {
  cursor: pointer;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
footer a.go img.cocktail {
  position: absolute;
  right: 0;
  top: -300px;
}
footer a.go p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 36px;
  /* 90% */
  text-transform: uppercase;
}
footer a.go .blue {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: #4AF6CD;
  width: 168px;
  height: 80px;
}
footer a.go .blue .mini-line {
  width: 20px;
  height: 1px;
  flex-shrink: 0;
  left: -5%;
  background: #FFF;
  position: absolute;
}
footer p.copyright {
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 14px;
  /* 116.667% */
  letter-spacing: 1.05px;
  text-transform: uppercase;
  opacity: 0.5;
  margin-bottom: 20px;
}
