article.surf {
  position: relative;
}
article.surf .btns {
  margin-right: 5vw;
  display: flex;
  gap: 20px;
  justify-content: end;
  position: relative;
  top: -50px;
  align-items: center;
}
article.surf .btns button {
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
article.surf .btns .left {
  transform: rotate(180deg);
}
article.surf .swiper {
  margin-bottom: 300px;
  width: 80vw;
  overflow-x: visible;
  overflow-y: visible;
}
article.surf div.toi {
  position: absolute;
  right: 0;
  top: 80px;
}
article.surf div.toi svg {
  width: 10vw;
}
article.surf div.map {
  width: 100vw;
  position: relative;
  top: 70px;
  margin-bottom: 200px;
}
article.surf div.map .main-map {
  width: 80vw;
  display: flex;
  justify-content: center;
}
article.surf div.map .main-map svg {
  right: -12%;
  position: relative;
  width: 80vw;
  height: 100%;
}
article.surf div.map .suff_currort1 {
  top: 55%;
  left: 12%;
}
article.surf div.map .suff_currort2 {
  top: 45%;
  left: 20%;
}
article.surf div.map .suff_currort3 {
  top: 78%;
  left: 36%;
}
article.surf div.map .suff_currort4 {
  top: 33%;
  left: 48%;
}
article.surf div.map .suff_currort5 {
  top: 85%;
  left: 52%;
}
article.surf div.map .suff_currort6 {
  top: 60%;
  left: 65%;
}
article.surf div.map .suff_currort7 {
  top: 77%;
  left: 82%;
}
article.surf div.map .suff_currort8 {
  top: 42%;
  left: 80%;
}
article.surf div.map .suff_currort {
  position: absolute;
  display: flex;
  width: 243px;
  flex-direction: column;
  gap: 10px;
  align-items: start;
}
article.surf div.map .suff_currort:hover .hidden {
  display: block;
}
article.surf div.map .suff_currort .hidden {
  animation: appear 1s ease;
  position: absolute;
  top: -210px;
  left: -40%;
  display: none;
  padding: 20px 32px;
  width: 243px;
  height: 196px;
  border-radius: 6px;
  background: #0f1010b1;
}
article.surf div.map .suff_currort .hidden h5.name {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
  line-height: 40px;
  text-transform: uppercase;
}
article.surf div.map .suff_currort .hidden .line-suff {
  width: 80px;
  height: 1px;
  background: #4AF6CD;
}
article.surf div.map .suff_currort .hidden p.local {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 10px;
}
article.surf div.map .suff_currort .hidden .weather {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
article.surf div.map .suff_currort .hidden .weather .elem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
article.surf div.map .suff_currort .hidden .weather .elem p.head-text {
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 800;
  line-height: 23px;
}
article.surf div.map .suff_currort .hidden .weather .elem p.desc {
  color: #FFF;
  text-align: center;
  font-family: Fira Sans;
  font-size: 10px;
  font-style: normal;
  font-weight: 300;
  line-height: 12px;
  text-transform: uppercase;
}
article.surf div.map .suff_currort .point {
  width: 1.5vw;
  height: 1.5vw;
  border-radius: 50%;
  background: #4AF6CD;
  filter: drop-shadow(0px 0px 35px rgba(74, 246, 205, 0.84));
  cursor: pointer;
}
article.surf .slide1 {
  background: url(../images/slide1.jpg);
}
article.surf .slide2 {
  background: url(../images/slide2.jpg);
  left: -30px;
  top: -30px;
}
article.surf .slide3 {
  background: url(../images/slide3.jpg);
  left: -60px;
  top: 60px;
}
article.surf .slide4 {
  background: url(../images/slide4.png);
  left: -90px;
  top: -30px;
}
article.surf .slide5 {
  left: -120px;
}
article.surf .slide6 {
  left: -150px;
}
article.surf .slide {
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  width: 390px;
  height: 650px;
}
article.surf .slide:hover .black-background {
  opacity: 0;
}
article.surf .slide:hover a.hidden {
  opacity: 1;
}
article.surf .slide .black-background {
  transition: all 0.5s 0.1s ease;
  background: #1C2121;
  opacity: 0.8;
  position: absolute;
  width: 100%;
  height: 100%;
}
article.surf .slide .content-slide {
  position: absolute;
  left: 40px;
  top: 70px;
  bottom: 80px;
  right: 40px;
}
article.surf .slide .content-slide h3 {
  color: #FFF;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 36px;
  /* 90% */
  text-transform: uppercase;
  width: 50%;
}
article.surf .slide .content-slide p.vertical {
  text-wrap: nowrap;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 0px;
  /* 122.222% */
  letter-spacing: 8px;
  text-transform: uppercase;
  transform: rotate(270deg);
  position: absolute;
  right: 20px;
  bottom: 250px;
  width: 500px;
}
article.surf .slide .content-slide p.vertical span {
  opacity: 0.5;
}
article.surf .slide a.hidden {
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.5s 0.1s ease;
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
article.surf .slide a.hidden p {
  color: #FFF;
  text-align: right;
  font-family: Fira Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 36px;
  /* 90% */
  text-transform: uppercase;
}
article.surf .slide a.hidden .blue {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: #4AF6CD;
  width: 168px;
  height: 80px;
}
article.surf .slide a.hidden .blue .mini-line {
  width: 20px;
  height: 1px;
  flex-shrink: 0;
  left: -5%;
  background: #FFF;
  position: absolute;
}
@keyframes appear {
  from {
    top: -180px;
    opacity: 0;
  }
  to {
    top: -210px;
    opacity: 1;
  }
}
